#recruit{
    background-color: #fff;
    padding: 120px 8vw;
}

.recruit_title{
    text-align: center;
}

.recruit_content{
    max-width: 1100px;
    margin: 70px auto 0;
}

.recruit_content table{
    width: 100%;
    border-collapse: separate;
    border-spacing: 0 20px;
}

.recruit_content th{
    width: 20%;
    font-size: 16px;
    line-height: 1;
    letter-spacing: 0.08rem;
    text-align: left;
    vertical-align: top;
    padding-top: 25px;
}

.recruit_content td{
    width: 80%;
    background-color: #F2F2F2;
    font-size: 16px;
    line-height: 28px;
    padding: 20px 30px;
    text-align: justify;
}

.table_navy_bg{
    background-color: #292F4A;
    margin: 25px 0 20px;
    padding: 0 20px 0.1em;
    border-radius: 5px;
    display: inline-block;
}

/*---------------------------------
    Tablet / タブレット版
----------------------------------*/
@media screen and (max-width:1100px){
    .recruit_content{
        padding: 0 40px;
    }
}

/*---------------------------------
    Mobile / スマホ版
----------------------------------*/
@media screen and (max-width:767px){
    #recruit{
        padding: 60px 20px;
    }

    .recruit_content{
        margin-top: 34px;
        padding: 0;
    }

    .recruit_content table{
        border-spacing: 0 15px;
    }

    .recruit_content th,
    .recruit_content td{
        display: block;
        width: 100%;
    }

    .recruit_content th{
        padding-top: 0;
    }

    .recruit_content td{
        margin-top: 10px;
        padding: 20px;
    }

    .table_navy_bg{
        background-color: #292F4A;
        margin: 15px 0;
        padding: 0 20px 0.1em;
    }
}